import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject } from 'rxjs';
import '../../models/group';

import { baseUrl } from '../../config/variables';

@Injectable({
  providedIn: 'root',
})
export class GroupsService {
  constructor(private http: HttpClient) {}

  groups = new BehaviorSubject<[Group]>(null);

  getAll() {
    return this.http.get(baseUrl + '/group').subscribe((res) => {
      if (res['data']) {
        this.groups.next(res['data']);
      }
    });
  }

  getOne(itemId) {
    return this.http.get(baseUrl + `/group/${itemId}`);
  }

  addOne(form) {
    return this.http.post(baseUrl + '/group', form);
  }

  updateOne(form, itemId) {
    return this.http.put(baseUrl + `/group/${itemId}`, form);
  }

  deleteOne(itemId) {
    return this.http.delete(baseUrl + `/group/${itemId}`);
  }

  lockOne(itemId) {
    return this.http.get(baseUrl + `/group/lock/${itemId}`);
  }

  unlockOne(itemId) {
    return this.http.get(baseUrl + `/group/unlock/${itemId}`);
  }
}
